import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';


@IonicPage()
@Component({
  selector: 'page-contact-full-details',
  templateUrl: 'contact-full-details.html',
})
export class ContactFullDetailsPage {
  selectedItem: any;
  submitAttempt: boolean = false;

  contactsForm: FormGroup;

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public formBuilder: FormBuilder) {
    this.selectedItem = navParams.get('item');

    this.saveContactFormValidate();
    

  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ContactFullDetailsPage');
  }

  saveContactForm() {
    this.submitAttempt = true;
    // Call for save

    

    if (this.contactsForm.valid) {
      // console.log('validddddddd----');
      alert('This functionality will be availabel at MISDesk CRM v2.34.0');
    } else {
      // console.log('innnnnnnnvalidddddddd----');
    }
  }  

  saveContactFormValidate() {
    const emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";

    this.contactsForm = this.formBuilder.group({
      "contactName": ['', Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])],

      "designationStatus": ['', Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*')])],

      "number1": ['', Validators.compose([Validators.minLength(10), Validators.maxLength(20), 
        Validators.pattern('[0-9 ]*'), Validators.required])],
      
      "email1": ['', Validators.compose([Validators.maxLength(100), 
        Validators.pattern(emailPattern), Validators.required])],

      "number2": ['', Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])],

      "number3": ['', Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])],

      "contactNote": ['', Validators.compose([Validators.minLength(4), Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])],
        
    });

  }  


}



